  .VideoGallery {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    max-width: calc(var(--width) * var(--cols));
  }
  
  .VideoView {
    width: var(--width);
    height: var(--height);
    background-color: #3a3a3e;
    video {
      height: 100%;
      width: 100%;
    }
  }
  
